@use "../abstracts/variables";
@use "../base/reset";

.primary-btn {
  display: flex;
  position: relative;
  align-items: center;
  justify-content: center;
  gap: 10px;
  border: variables.$main-border;
  padding: 12px 24px;
  font-size: 1.125rem;

  img {
    height: 1.25rem;
    width: 1.25rem;
  }
}

